import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import TableCell from "@mui/material/TableCell";

const dispatchedEvents = [];

const SrsMuiTableCell = function (props, ref) {
  return (
    <TableCell {...props} theme={props.muiTheme}>
      <slot />
    </TableCell>
  );
};

const styles = `
  /* Custom styles for the MuiTableCell component */
`;

const props = [
  {
    name: "align",
    defaultValue: "inherit",
    type: "string",
  },
  {
    name: "padding",
    defaultValue: "normal",
    type: "string",
  },
  {
    name: "size",
    defaultValue: "medium",
    type: "string",
  },
  {
    name: "sortDirection",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to MuiTableCell if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTableCell),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
